
<!---->

<template>
    <div class="root">
        <!---->
        <header id="head_top">
            <router-link to="" ><i class="el-icon-search"></i></router-link>
            <router-link to="/Home">
                <span class=" title_text ellipsis">{{addressName.name}}</span>
            </router-link>
            <router-link to="/login"  v-if="!isShow">
                <span>|</span>
            </router-link>
            <router-link to="/profile" v-if="isShow">
                 <span class="user">
                    <i class="el-icon-user"></i>
                </span>
            </router-link>
        </header>
        <!---->
        <div class="msite_nav">
        <div class="swiper-container" >
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                    <router-link v-for="food in foodArr1" :to="{path:'/food',query:{title:food.title,geohash: geohash ,restaurant_category_id:food.id,latitude:latitude,longitude:longitude}}" :key="food.title">
                        <div class="pic">
                            <img :src="'//cube.elemecdn.com'+food.image_url" alt="">
                        </div>
                        <p>{{food.title}}</p>
                    </router-link>
                </div>
                <div class="swiper-slide">
                    <router-link v-for="food in foodArr2" :to="{path:'/food',query:{title:food.title,geohash: geohash ,restaurant_category_id:food.id,latitude:latitude,longitude:longitude}}" :key="food.title">
                        <div class="pic">
                            <img :src="'//cube.elemecdn.com'+food.image_url" alt="">
                        </div>
                        <p>{{food.title}}</p>
                    </router-link>
                </div>
            </div>
            <!--  -->
            <div class="swiper-pagination"></div>
        </div>
        </div>
        <!---->
        <div class="shop_list_container">
            <header class="shop_header">
                <i class="el-icon-s-shop"></i>
                <span></span>
            </header>
            <div class="shoplist_container">
                <ul>
                    <li class="shop_li" v-for="shop in shopArr" @click="shopP(shop)">
                        <section>
                            <img :src="'https://elm.cangdu.org/img/'+shop.image_path" alt="" class="shop_img">
                        </section>
                        <hgroup class="shop_right">
                            <header class="shop_detail_header">
                                <h4 class="shop_title ellipsis">
                                <span class="span1"></span>                                              <span>{{shop.name}}</span>
                                </h4>
                                <ul class="shop_detail_ul">
                                    <li v-for="pro in shop.supports" class="support">{{pro.icon_name}}</li>
                                </ul>
                            </header>
                            <h5 class="rating_order_num">
                                <section class="rating_order_num_left">
                                    <section class="rating_section">
                                        <div class="rating_container">
                                            <el-rate
                                                    v-model="shop.rating"
                                                    disabled
                                                    text-color="#ff9900"
                                                    class="start"
                                            >
                                            </el-rate>
                                        </div>
                                        <span class="rating_num">{{shop.rating}}</span>
                                        <section class="order_section">
                                            :{{shop.recent_order_num}}
                                        </section>
                                    </section>
                                </section>
                                <section class="rating_order_num_right">
                                    <span class="delivery_left delivery_style">{{shop.delivery_mode.text}}</span>
                                    <span class="delivery_right delivery_style">{{shop.supports[1].name}}</span>
                                </section>
                            </h5>
                            <h5 class="fee_distance">
                                <p class="fee">
                                    {{""+shop.float_minimum_order_amount+""}}
                                    <span>/</span>
                                    {{shop.piecewise_agent_fee.tips}}
                                </p>
                                <p class="distance_time">
                                    <span>{{shop.distance}}</span>
                                    <span class="segmentation">/</span>
                                    <span class="order_time">{{shop.float_minimum_order_amount}}{{shop.order_lead_time}}</span>
                                </p>
                            </h5>
                        </hgroup>

                    </li>
                </ul>
            </div>
        </div>
        <!---->
        <section id="foot_guide">
            <section class="guide_item guide_item1">
                <i class="el-icon-eleme"></i>
                <span></span>
            </section>
            <section class="guide_item" @click="goSearch">
                <i class="el-icon-search"></i>
                <span></span>
            </section>
            <section class="guide_item" @click="goOrder">
                <i class="el-icon-tickets"></i>
                <span></span>
            </section>
            <section class="guide_item" @click="goMy">
                <i class="el-icon-user"></i>
                <span></span>
            </section>
        </section>
    </div>
<!--http://elm.cangdu.org/img/-->
</template>

<script>
    import Swiper from 'swiper'
    import "../../node_modules/swiper/css/swiper.css"
    export default {
        name: "Msite",
        data(){
            return {
                geohash:"",
                addressName:"",
                //1
                foodArr1:[],
                //2
                foodArr2:[],
                //
                foodArr:[],
                //
                shopArr:[],
                support:[],
                isShow:true,
                localAddress:[],
                latitude:"",
                longitude:"",
                //
                address:[],
                placeHistory:[],
                //
                end:[],

            }

        },
        created(){

            this.placeHistory = JSON.parse(localStorage.getItem('placeHistory'))
            // console.log(this.placeHistory);
            this.end = this.placeHistory?this.placeHistory.slice(-1):null
            // console.log(this.end)
            //
            if (this.placeHistory == null) {
                this.address ={
                    name: "",
                    address: "753",
                    latitude: 31.14419,
                    longitude: 121.66034,
                    geohash: "31.14419,121.66034"
                }
                this.latitude = this.address.latitude;
                this.longitude = this.address.longitude;
                this.geohash = this.address.geohash;

            }else {
                //
                this.geohash = this.end[0].geohash
                this.latitude = this.end[0].latitude;
                this.longitude = this.end[0].longitude;
            }
            this.axios.get("v2/pois/"+this.geohash,{
                params:{
                    geohash:this.geohash
                }
            }).then(res=>{
                // console.log(res);
                this.addressName = res;
            }).catch(err=>{
            })
            //
            this.axios.get("shopping/restaurants",{
                params:{
                    latitude:this.latitude,
                    longitude:this.longitude
                }
            }).then(res=>{
                // console.log(res);
                this.shopArr = res;
            }).catch(err=>{})
            //
            this.axios.get("v2/index_entry").then(res=>{
                this.foodArr = res;
                for (let i = 0; i<res.length/2;i++){
                    this.foodArr1.push(res[i]);
                }
                for (let i = res.length/2; i<res.length;i++){
                    this.foodArr2.push(res[i]);
                }
                // console.log(this.foodArr1,this.foodArr2);
            }).catch(err=>{})
            if (localStorage.getItem('user')){
                this.isShow = true
            }else {
                this.isShow = false;
            }
        },
         mounted(){
            //
             new Swiper ('.swiper-container', {
                direction: 'horizontal', // 
                // loop: true, // 
                // 
                pagination: {
                    el: '.swiper-pagination',
                },
                ap1:{
                    freeMode:false
                }
            })
        },
        computed:{
            user(){
                if (localStorage.getItem('user')){
                    this.isShow = true
                }else {
                    this.isShow = false;
                }
            }
        },
        methods:{
            shopP(shop){
                console.log(shop.id)
                this.$router.push({
                    path:"/shop",
                    query:{
                        shopid:shop.id,
                        latitude:shop.latitude,
                        longitude:shop.longitude,
                        geohash:this.$route.query.geohash,
                    }

                })
            },
            goMy(){
                this.$router.push({
                    path:'/profile',
                    query:{
                        geohash:this.$route.query.geohash,
                    }
                })
            },
            goSearch(){
                this.$router.push({
                    path:'/search',
                })
            },
            goOrder(){
                this.$router.push({
                    path:'/order',
                })
            }
        }
    }
</script>

<style>
    .root {
        height: 100%;
    }
    #head_top {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    #head_top>a{
        text-decoration: none;
    }
    #head_top>a:nth-of-type(1) {
        left: .8rem;
        width: .9rem;
        height: .9rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: .85rem;
        color: #fff;
    }
    #head_top>a:nth-of-type(2) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
        margin-left: -.5rem;
    }
    #head_top>a:nth-of-type(3) {
        right: .55rem;
        font-size: .65rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .title_text {
         font-size: .8rem;
         color: #fff;
         text-align: center;
         display: block;
    }
    .login_span {
        color: #fff;
    }
    .msite_nav {
        padding-top: 2.1rem;
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        height: 8.6rem;
    }
    .swiper-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: -ms-flexbox;
        display: flex;
        transition-property: transform;
        box-sizing: content-box;
    }
    .msite_nav .swiper-container {
        width: 100%;
        height: 80%;
        padding-bottom: .6rem;
    }
    .swiper-container {
        margin-left: auto;
        margin-right: auto;
         position: relative;
        overflow: hidden;
        z-index: 1;
    }
    .msite_nav .swiper-container .swiper-pagination {
        bottom: 0rem;
    }
    .swiper-container-horizontal>.swiper-pagination-bullets{
        bottom: 0px;
        left: 0;
        width: 100%;
    }
    .swiper-pagination {
        position: absolute;
        text-align: center;
        transition: .3s;
        transform: translateZ(0);
        z-index: 10;
        bottom: .01rem;
    }
    .swiper-slide a{
        width: 25%;
        padding: .3rem 0;
        /*display: flex;*/
        /*justify-content: center;*/
        float: left;
        text-decoration: none;
    }
    .swiper-slide>a img {
        margin-bottom: .3rem;
        width: 1.8rem;
        height: 1.8rem;
        /*opacity: 1;*/
    }
    .swiper-slide p {
        text-align: center;
        font-size: .55rem;
        color: #666;
        margin-top: .2rem;
    }
    .pic {
        text-align: center;
    }
    .shop_list_container{
        margin-top: .4rem;
        border-top: .025rem solid #e4e4e4;
        background-color: #fff;
    }
    .shop_header>i {
        color: #999;
        margin-left: .6rem;
        vertical-align: middle;
        width: .6rem;
        height: .6rem;
        /*padding-top: .5rem;*/
    }
    .shop_header>span {
        color: #999;
        font: .55rem/1.6rem Microsoft YaHei;
        margin-left: .5rem;
    }
    .shoplist_container {
        background-color: #fff;
        margin-bottom: 2rem;
    }
    .shop_li{
        display: flex;
        border-bottom: .025rem solid #f1f1f1;
        padding: .7rem .4rem;
    }
    .shop_img{
        width: 2.7rem;
        height: 2.7rem;
        display: block;
        margin-right: .4rem;
    }
    .shop_right {
        flex: auto;
    }
    .shop_detail_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .shop_title{
        width: 8.5rem;
        color: #333;
        padding-top: .01rem;
        font: .65rem/.65rem PingFangSC-Regular;
        font-weight: 700;
    }
    .span1 {
        display: inline-block;
        font-size: .5rem;
        line-height: .6rem;
        color: #333;
        background-color: #ffd930;
        padding: 0 .1rem;
        border-radius: .1rem;
        margin-right: .2rem;
    }
    hgroup {
        display: block;
    }
    .shop_detail_ul {
        display: flex;
        transform: scale(.8);
        margin-right: -.3rem;
    }
    .shop_detail_ul{
        display: flex;
        transform: scale(.8);
        margin-right: -.3rem;
    }
    .support {
        font-size: .5rem;
        color: #999;
        border: .025rem solid #f1f1f1;
        padding: 0 .04rem;
        border-radius: .08rem;
        margin-left: .05rem;
    }
    .rating_order_num {
        display: flex;
        justify-content: space-between;
        height: .6rem;
        margin-top: .52rem;
    }
    .rating_order_num_left {
        display: flex;
        justify-content: flex-start;
    }
    .rating_section {
        display: flex;
    }
    .rating_container {
        position: relative;
        top: .2rem;
        width: 1.7rem;
        height: .4rem;
    }
    .rating_num {
        font-size: .4rem;
        color: #ff6000;
        margin: 0 .2rem;
    }
    .order_section {
        transform: scale(.8);
        margin-left: -.2rem;
        font-size: .4rem;
        color: #666;
    }
    .rating_container /deep/ .el-rate {
        width: 2rem;
        margin-top: -.5rem;
    }
    .el-rate__icon {
        font-size: 5px;
        margin-right: -5px;
        color: #C0C4CC;
        -webkit-transition: .3s;
        transition: .3s;
    }
    .rating_order_num_right {
        display: flex;
        align-items: center;
        transform: scale(.7);
        min-width: 5rem;
        justify-content: flex-end;
        margin-right: -.8rem;
    }
    .delivery_left {
        color: #fff;
        background-color: #3190e8;
        border: .025rem solid #3190e8;
        font-size: .4rem;
        padding: .04rem .08rem 0;
        border-radius: .08rem;
        margin-left: .08rem;
    }
    .delivery_right {
        color: #3190e8;
        border: .025rem solid #3190e8;
        font-size: .4rem;
        padding: .04rem .08rem 0;
        border-radius: .08rem;
        margin-left: .08rem;
    }
    .fee_distance {
        margin-top: .52rem;
        display: flex;
        justify-content: space-between;
        font-size: .5rem;
        color: #333;
    }
    .fee{
        transform: scale(.9);
        font-size: .5rem;
        color: #666;
        width: 52%;
        margin-left: -.2rem;
     }
    .distance_time {
        transform: scale(.9);
        margin-right: -.6rem;
        /*margin-left: -.3rem;*/
        width: 75%;
    }
    .segmentation {
        color: #ccc;
    }
    .order_time {
        color: #3190e8;
    }
    #foot_guide {
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 2rem;
        display: flex;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    .guide_item {
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .guide_item span{
        font-size: .45rem;
        color: #666;
        margin-top: .2rem;
        text-align: center;
        margin-left: .2rem;
    }
    .guide_item i {
        width: .8rem;
        height: .8rem;
        margin-top: .3rem;
        fill: #ccc;
    }
    .guide_item1 i {
        color: #3190e8;
    }
    .user i {
        font-size: 1rem;
    }
</style>